#posts{
    display: flex;
    margin-left: 10vh;
}

#post{
    display: flex;
    flex-direction: column;
}
#post #desc{
    font-size: 5vh;
    margin-top: 3vh;
    margin-bottom: 4vh;
}
#post #over{
    display: inline-flex;
    margin-top: 4vh;
    font-style: italic;
}

#post #avatar {
    margin-right: 1vh;
    width: 4vh;
    height: 4vh;
    margin-bottom: 2vh;
}